.merchandise_BG {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: 1000;
}

.items_container {
  width: 90%;
  height: 75vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
  flex: 1 1 30%;
}

.merchandise_title {
  height: 14vh;
  padding-top: 5%;
  top: 15vh;
}

.merchandise_title_image {
  margin: auto;
  display: flex;
  position: relative;
  width: 600px;
  height: 61px;
  z-index: 1003;
}

.left_item {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.center_item {
  display: flex;
  justify-content: center;
  flex: 1;
  margin: auto;
}

.right_item {
  display: flex;
  justify-content: center;
  flex: 1;
}

.item {
  position: relative;
  z-index: 1005;
  padding: 25px;
}

.photocard {
  display: inline;
}

.photocard:hover {
  background-image: url("../assets/isedolMerchandise/photocard_hover.png");
  background-size: 100% 100%;
}

.acrylic {
  width: 100%;
  height: 100%;
}

.acrylic:hover {
  background-image: url("../assets/isedolMerchandise/acrylic_stand_hover.png");
  background-size: 100% 100%;
}

.poster:hover {
  background-image: url("../assets/isedolMerchandise/poster_hover.png");
  background-size: 100% 100%;
}

.sticker:hover {
  background-image: url("../assets/isedolMerchandise/sticker_hover.png");
  background-size: 100% 100%;
}

.hidden_created {
  position: absolute;
  z-index: 9999;
  bottom: 8vh;
  right: 10vw;
  color: white;
  opacity: 0;
  transition: all 1s ease;
  font-size: small;
}

.hidden_created:hover {
  opacity: 1;
}
